<template>
	<div class="mt-24">
		<h4 class="m-0 mb-12 c-3" v-if="hactive">
			{{header}}
		</h4>
		<h4 class="m-0 mb-12" v-else>
			{{header}}
		</h4>
		<input type="text" class="text inp" :value="value"  :placeholder="placeholder">
	</div>
</template>

<script>
export default {
	name: "Input",
	props:[
		"header",
		"placeholder",
		"hactive",
		"disabled",
		"value",
		"w",
		"h"
	],
	mounted(){
		if(this.disabled){
			document.querySelector(".inp").disabled = true
		}
		if(this.w || this.h){
			for(let i = 0; i<3; i++){
				document.querySelectorAll(".inp")[i].style.width = this.w + "px"
				document.querySelectorAll(".inp")[i].style.height = this.h + "px"
				console.log(this.w)
			}
		}
		
	}
}
</script>

<style scoped>
input{
	border: 1px solid #CBC8CC;
	padding-left: 13px;
}
</style>