<template>
	<div class="card mt-16 mb-16">
			<div class="image">
				<img v-if="image == 1" src="../assets/items/item1.svg" alt="">
				<img v-if="image == 2" src="../assets/items/item2.svg" alt="">
				<img v-if="image == 3" src="../assets/items/item3.svg" alt="">
				<img v-if="image == 4" src="../assets/items/item4.svg" alt="">
				<img v-if="image == 5" src="../assets/items/item5.svg" alt="">
				<img v-if="image == 6" src="../assets/items/item6.svg" alt="">
			</div>
			<div class="like">
				<img v-if="liked == false" src="../assets/heart.svg" alt="">
				<img v-if="liked == true" src="../assets/heart_fill.svg" alt="">
			</div>
			<div class="name">
				<div class="title">
					<h4 class="m-0">
						{{title}}
					</h4>
				</div>
				<div class="author text pt-17">
					<img src="../assets/profile.svg" alt="">
					{{author}}
				</div>
			</div>
			<div class="bottom c-6">
				<div class="flex-b">
					<div class="price">
						<div class="footnote">
							Price
						</div>
						<div class="text">
							Ξ{{price}}
						</div>
					</div>
					<div class="end">
						<div class="footnote">
							Sale ends in
						</div>
						<div class="text">
							{{end}}
						</div>
					</div>
				</div>
			</div>
	</div>
</template>
<script>
export default {
	name: 'Card',
	props: [
		"author",
		"title",
		"price",
		"end",
		"liked",
		"image"
	]
}
</script>
<style scoped>
.card {
	position: relative;
	display: flex;
	flex-direction: column;
	word-wrap: break-word;
	background-color: #fff;
	background-clip: border-box;
	border: 1px solid rgba(0, 0, 0, 0.125);
	border-radius: 0.25rem;
	width: 280px;
	height: 404px;
}
.image{
	width: 100%;
	height: 192px;
}
.like{
	position: absolute;
	right: 12px;
	top: 12px;
	height: 24px;
	width: 24px;
	z-index: 5;
}
.name{
	padding: 16px;
}
.bottom{
	height: 82px;
	background: #510070;
	padding: 16px;
}
</style>