<template>
	<div id="right">
		<div class="top">
				<div class="cards">
					<Msg :icon="'bid'" :text="'Bids'" :value="$store.state.bids" />
					<Msg :icon="'tag'" :text="'Price'" :value="'Ξ'+$store.state.price" />
					<Msg :icon="'clock'" :text="'Bidding will end in'" :value="$store.state.bidsT" />
				</div>
			</div>
			<div class="bottom">
				<div class="bottomL flex-a">
					<div class="dots">
						<Bullet :active="this.slideIndex == 1 ? true : false" :tick="time" />
						<Bullet :active="this.slideIndex == 2 ? true : false" :tick="time" />
						<Bullet :active="this.slideIndex == 3 ? true : false" :tick="time" />
						<Bullet :active="this.slideIndex == 4 ? true : false" :tick="time" />
					</div>

					<Message :header="'11Lorem ipsum dolor sit amet, consectetur'" :text="'Morbi convallis ut tellus eu blandit. Praesent finibus nisi et dolor tincidunt vestibulum. Vivamus sodales ultrices tellus, eu imperdiet nisl ultrices vel. Pellentesque gravida ut du'" />
					<Message :header="'22Lorem ipsum dolor sit amet, consectetur'" :text="'Morbi convallis ut tellus eu blandit. Praesent finibus nisi et dolor tincidunt vestibulum. Vivamus sodales ultrices tellus, eu imperdiet nisl ultrices vel. Pellentesque gravida ut du'" />
					<Message :header="'33Lorem ipsum dolor sit amet, consectetur'" :text="'Morbi convallis ut tellus eu blandit. Praesent finibus nisi et dolor tincidunt vestibulum. Vivamus sodales ultrices tellus, eu imperdiet nisl ultrices vel. Pellentesque gravida ut du'" />
					<Message :header="'44Lorem ipsum dolor sit amet, consectetur'" :text="'Morbi convallis ut tellus eu blandit. Praesent finibus nisi et dolor tincidunt vestibulum. Vivamus sodales ultrices tellus, eu imperdiet nisl ultrices vel. Pellentesque gravida ut du'" />
					<div class="arrows flex-a flex-v">
						<div class="up"  v-on:click="prevSlide">
							<img src="../assets/ArrowUp.svg" class="arrow" alt="">
						</div>
						<div class="down" v-on:click="nextSlide">
							<img src="../assets/ArrowDown.svg" class="arrow" alt="">
						</div>
					</div>
				</div>
				<div class="bottomR" />
			</div>
	</div>
</template>

<script>
import Msg from '../components/msg.vue'
import Bullet from '../components/bullet.vue'
import Message from '../components/message.vue'

export default {
	name: 'Right',
	components:{
		Msg,
		Bullet,
		Message
	},
	mounted(){
		// document.onreadystatechange = () => {
		// 	if (document.readyState == "complete") {
		// 		let slides = document.getElementsByClassName("message");
		// 		for (let slide of slides) {
		// 			slide.style.display = "none";
		// 		}
		// 		let first = document.getElementsByClassName("message")[0];
		// 		first.style.display = "block"
		// 		console.log("loaded")
		// 	}
		// 	console.log("ready")
		// }	
		let first = document.getElementsByClassName("message")[0];
		first.style.display = "block"
		// console.log("mounted")
		
		this.ScrollAuto()
	},
	methods:{
		ScrollAuto: function(){
			setInterval(()=> {
				this.nextSlide()
				this.tick()
			}, 6000)
			//console.log("aabb")
		},
		tick: function(){
			//console.log(this.time)
			this.time++
			if (this.time>30){
			clearTimeout(this.timer); // таймер остановится на нуле
			this.time = 0
			}
			else {
				this.timer = setTimeout(this.tick, 200);
			}
		},
		nextSlide: function(){
			this.showSlides(this.slideIndex += 1);

		},
		prevSlide: function() {
			this.showSlides(this.slideIndex -= 1);  

		},
		showSlides: function(n){
			let slides = document.getElementsByClassName("message");
			
			if (n > slides.length) {
      this.slideIndex = 1
			}
			if (n < 1) {
				this.slideIndex = slides.length
			}
			console.log(slides)
			for (let slide of slides) slide.style.display = "none";
			
			slides[this.slideIndex - 1].style.display = "block"; 
		
			console.log(n + "\t" + slides.length)
		}
	},
	data: function () {
		return {
			slideIndex: 1,
			timer: '',
			time: 0
		}
	}
}
</script>

