import Vue from 'vue';
import VueRouter from 'vue-router';
Vue.use(VueRouter);

import HelloWorld from '../pages/HelloWorld.vue';
import HomePage from '../pages/HomePage.vue';
import ExploreMore from '../pages/ExploreMore.vue';
import Login from "../pages/Login.vue";
import Signup from "../pages/Signup.vue";
import Landing from "../pages/Landing.vue";
import Authorization from "../pages/Authorization.vue";
import Authorization2 from "../pages/Authorization2.vue";
import Authorization3 from "../pages/Authorization3.vue";
import Authorization4 from "../pages/Authorization4.vue";
import Authorization5 from "../pages/Authorization5.vue";
import Authorization6 from "../pages/Authorization6.vue";
import Authorization7 from "../pages/Authorization7.vue";


const routes = [
	{
		path: "",
		component: HomePage
	},
	{
		path: "/home",
		component: HomePage
	},
	{
		path: "/hello",
		component: HelloWorld
	},
	{
		path: "/more",
		component: ExploreMore
	},
	{
		path: "/login",
		component: Login
	},
	{
		path: "/signup",
		component: Signup
	},
	{
		path: "/landing",
		component: Landing
	},
	{
		path: "/auth",
		component: Authorization
	},
	{
		path: "/auth2",
		component: Authorization2
	},
	{
		path: "/auth3",
		component: Authorization3
	},
	{
		path: "/auth4",
		component: Authorization4
	},
	{
		path: "/auth5",
		component: Authorization5
	},
	{
		path: "/auth6",
		component: Authorization6
	},
	{
		path: "/auth7",
		component: Authorization7
	},

];

export default new VueRouter({
	routes,
	mode: "history",
});

