<template>
  <div id="userslider">
		<div class="flex-s mb-48 ml-70">
			<h4 class="c-1 mr-76 mt-0 mb-0 flex-center">
				+25,000 creators
			</h4>
			<div class="flex z-4">
				<div class="arrow m-0" v-on:click="prevSlide">
					<img src="../assets/arrowL.svg" alt="" id="usrArrL" class="mr-24" style="filter: contrast(80%);">
				</div>
				<div class="arrow m-0" v-on:click="nextSlide">
					<img src="../assets/arrowR.svg" alt="" id="usrArrR" class="ml-24">
				</div>
			</div>
		</div>
		<div class="slider flex-s">
			<component :is="'Card'" :name="'DonPedro'" :id="'@invisibletter'"
			:desc="'Florence To is a Scottish-born, Hong Kong artist and director creating sound + light installations with a focus on architectural spatial design.'" 
			:followers="'365'" :followed="true" :image="1" :pic="2" />
			
			<component :is="'Card'" :name="'DonPedro'" :id="'@invisibletter'"
			:desc="'Florence To is a Scottish-born, Hong Kong artist and director creating sound + light installations with a focus on architectural spatial design.'" 
			:followers="'104k'" :followed="false" :image="2" :pic="2" :online="true" />
			
			<component :is="'Card'" :name="'DonPedro'" :id="'@invisibletter'"
			:desc="'Florence To is a Scottish-born, Hong Kong artist and director creating sound + light installations with a focus on architectural spatial design.'" 
			:followers="'365'" :followed="true" :image="3" :pic="2" />
			
			<component :is="'Card'" :name="'DonPedro'" :id="'@invisibletter'"
			:desc="'Florence To is a Scottish-born, Hong Kong artist and director creating sound + light installations with a focus on architectural spatial design.'" 
			:followers="'365'" :followed="true" :image="4" :pic="2" />
			
			<component :is="'Card'" :name="'DonPedro'" :id="'@invisibletter'"
			:desc="'Florence To is a Scottish-born, Hong Kong artist and director creating sound + light installations with a focus on architectural spatial design.'" 
			:followers="'365'" :followed="true" :image="5" :pic="2" />
			<!-- -->
			<component :is="'Card'" :name="'DonPedro'" :id="'@invisibletter'"
			:desc="'Florence To is a Scottish-born, Hong Kong artist and director creating sound + light installations with a focus on architectural spatial design.'" 
			:followers="'365'" :followed="true" :image="3" :pic="2" />
			
			<component :is="'Card'" :name="'DonPedro'" :id="'@invisibletter'"
			:desc="'Florence To is a Scottish-born, Hong Kong artist and director creating sound + light installations with a focus on architectural spatial design.'" 
			:followers="'365'" :followed="true" :image="1" :pic="2" />
			<component :is="'Card'" :name="'DonPedro'" :id="'@invisibletter'"
			:desc="'Florence To is a Scottish-born, Hong Kong artist and director creating sound + light installations with a focus on architectural spatial design.'" 
			:followers="'104k'" :followed="false" :image="2" :pic="2" :online="true" />
			
			<component :is="'Card'" :name="'DonPedro'" :id="'@invisibletter'"
			:desc="'Florence To is a Scottish-born, Hong Kong artist and director creating sound + light installations with a focus on architectural spatial design.'" 
			:followers="'365'" :followed="true" :image="3" :pic="2" />
			
		</div>
		<div class="bg">
			<div class="buble"></div>
		</div>
  </div>
</template>

<script>
import Card from './carduser.vue'
export default {
  name: 'itemslider',
  components: {
    Card
  },
	mounted(){
		document.onreadystatechange = () => {
			if (document.readyState == "complete") {
				let slides = document.getElementsByClassName("usrcard");
				for (let slide of slides) {
					slide.style.display = "block";
				}
				let first = document.getElementsByClassName("usrcard")[0];
				first.style.display = "block"
			}
		}	
	},
	methods:{
		nextSlide: function(){
			this.showSlides(this.slideIndex += 1);
		},
		prevSlide: function() {
			this.showSlides(this.slideIndex -= 1);  
		},
		showSlides: function(n){
			let slides = document.getElementsByClassName("usrcard");
			
			if (n > slides.length) this.slideIndex = slides.length;
			
			if (n < 1) this.slideIndex = 1;
			
			if(n == slides.length+1) return

			for (let slide of slides) slide.style.display = "block";
			
			for(let i=0; i < n-1; i++) slides[i].style.display = "none";
			
			if(n != 1 && n != 0) document.getElementById('usrArrL').style.filter = "contrast(100%)"
			else document.getElementById('usrArrL').style.filter = "contrast(80%)"
			
			if(n == slides.length) document.getElementById('usrArrR').style.filter = "contrast(80%)"
			else document.getElementById('usrArrR').style.filter = "contrast(100%)"
			
			console.log(n + "\t" + slides.length)
		}
	},
	data: function () {
		return {
			slideIndex: 1
		}
	}
}
</script>

<style scoped>
#userslider{
  height: 900px;
	min-height: 900px;
	width: 100%;
	padding: 66px 0 135px 0px;
	background: #F2F1F3;
	overflow: hidden;
}
.arrow{
	width: 64px;
	height: 64px;
}
.slider{
	width: 100%;
	overflow: hidden;
}
.bg{
	position: relative;
	top: -790px;
	left: 370px;
	height: 920px;
	width: 1550px;
	z-index: 1;
	background: #510070;
}
.buble{
	position: relative;
	top: -760px;
	left: 750px;
	border-radius: 50%;
	width: 1804px;
	height: 1804px;
	background: #7D01AD;
}
.z-4{
	z-index: 4;
}
</style>
