<template>
	<div id="navbar">
		<div class="flex-b">
			<div class="flex-s">
				<router-link to="/" class="mr-80 mt-6">
					<img src="../assets/NFT.svg">
				</router-link>
				<div class="menu flex-b pt-15">
					<a class="link">Marketplace</a>
					<a class="link">Drops</a>
					<a class="link">Artists</a>
					<a class="link">Events</a>
					<a class="link">News</a>
					<a class="link">About Us</a>
				</div>
			</div>
			<div class="flex-e">
				<div>
					<input type="search" placeholder="Search by users, arts, drops and more" name="" id="searchbox" class="text hidden">
					<img v-if="$store.state.logined == false" src="../assets/Search.svg" alt="" class="search" id="search">
					<img v-if="$store.state.logined == true" src="../assets/Search.svg" alt="" class="search logined" id="search">
				</div>
				<div v-if="$store.state.logined == false" class="ml-28">
					<router-link to="/login" class="login mt-18 footnote">
						Log in
					</router-link>
					<router-link to="/signup" class="ml-24">
						<button class="bt h-56 w-130 sign">
							Sign up
						</button>
					</router-link>
				</div>
				<div v-if="$store.state.logined == true" class="flex mt-16">
					<div class="mr-17 ml-8">
						<img v-if="$store.state.notify == false" src="../assets/notification.svg" class="notify" id="notification">
						<img v-if="$store.state.notify == true" src="../assets/notification_yes.svg" class="notify" id="notification">
					</div>
					<div id="usr" class="usr">
						<img src="../assets/user.svg" class="uicon" alt="">
						<div class="ml-8 footnote balance">
							Ξ{{$store.state.balance}}
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	name: 'Navbar',
	mounted(){
		let Shidden = true;
		let searchbox = document.getElementById("searchbox");
		let search = document.getElementById("search");
		search.addEventListener("click", 
			function unhide(){
				if(Shidden){
					searchbox.classList.remove("hidden")
					Shidden = false
				}
				else{
					searchbox.classList.add("hidden")
					Shidden = true
				}
			}
		)
		let Nhidden = true;
		let notify = document.getElementById("notify");
		let notification = document.getElementById("notification");
		notification.addEventListener("click", 
			function unhide(){
				if(Nhidden){
					notify.classList.remove("hidden")
					Nhidden = false
				}
				else{
					notify.classList.add("hidden")
					Nhidden = true
				}
			}
		)
		let Chidden = true;
		let usr = document.getElementById("usr");
		let check = document.getElementById("check");
		usr.addEventListener("click", 
			function unhide(){
				if(Chidden){
					check.classList.remove("hidden")
					Chidden = false
				}
				else{
					check.classList.add("hidden")
					Chidden = true
				}
			}
		)
	}
}
</script>
<style scoped>
#navbar{
	padding-left: 70px;
	padding-right: 70px;
	padding-top: 12px;
	position: fixed;
	height: 80px;
	left: 0px;
	top: 0px;
	background: #FFFFFF;
	z-index: 10;
	width: 100%;
	border-bottom: 1px solid black;
}
.login{
	display:inline-block;
	width: 44px;
	color: #000;
}
.menu{
	width: 555px;
}
.balance{
	width: 40px;
	margin-top: 2px;
}
.uicon{
	width: 24px;
	height: 24px;
}
.link{
	font-family: Poppins;
	font-style: normal;
	font-weight: normal;
	font-size: 18px;
	line-height: 140%;
	letter-spacing: 0.75px;
	color: #000000;
}
.usr{
	display: flex;
	width: 81px;
}
.sign{
	padding: 15.5px 28.5px;
}
.search{
	position: absolute;
	top: 28px;
	right: 308px;
	fill: black;
}
.logined{
	position: absolute;
	top: 28px;
	right: 209px;
	fill: black;
}
input{
	width: 580px;
	height: 56px;
	padding-left: 12px;
	border: 1px solid #CBC8CC;
}
</style>
