	<div class="auth">
		<div class="nav">
			<div class="flex-s">
				<router-link to="/" class="navbar-brand mr-80">
					<img src="../assets/NFT.svg" class="nft-logo" alt="">
				</router-link>
			</div>
		</div>
		
		<div class="card">
			<div class="back bc-1" />
			<div class="pic flex-c">
				<h1 class="a m-0 mt-5">a</h1>
			</div>
			<div class="flex-v info">
				<div class="name center h4 mb-8">
					Enter 6-digit code we emailed to
				</div>
				<div class="center text bold">
					invisibletter.kk@gmail.com
				</div>
				<div class="flex mb-20 mt-14 mb-24">
					<img src="../assets/email_24px.svg" class="mr-11">
					<div class="flex mr-16	subtext">
						<input type="text" placeholder="" class="m-0 subtext">
						<input type="text" placeholder="" class="m-0 subtext">
						<input type="text" placeholder="" class="m-0 subtext">
					</div>
					<div class="flex">
						<input type="text" placeholder="" class="m-0 subtext">
						<input type="text" placeholder="" class="m-0 subtext">
						<input type="text" placeholder="" class="m-0 subtext">
					</div>
				</div>
				<div class="footnote c-1 center">
					Your code will arrive in a few sec.
				</div>
				<div class="footnote c-3 center mt-8" id="timerblock">
					Resend Code (<span id="timer" />)
				</div>
				<button class="bt h-40 w-280 hidden mt-3" id="resend">
					Resend
				</button>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	name: "Authorization",
	mounted(){
		let a = Date.now();
		let countDownDate = a + 32000;
		let timerId = setInterval(function() {
			let now = new Date().getTime();
			console.log(now);
			let distance = countDownDate - now;
			let seconds = Math.floor((distance % (1000 * 60)) / 1000);
			document.getElementById("timer").innerHTML = seconds + "s";
			console.log(seconds)
			if(seconds == 0){
				clearTimeout(timerId);
				document.getElementById("timerblock").style.display = "none";
				document.getElementById("resend").classList.remove("hidden");
			} 
		}, 1000);
		
	}
}
</script>

