<template>
	<div class="bullet mt-16 mb-16">
		<div class="point" />
		<div v-if="active == true" class="circle">
			<svg
				class="progress-ring"
				width="18"
				height="18">
				<circle
					id="pr"
					class="progress-ring__circle"
					stroke="white"
					stroke-width="2"
					fill="transparent"
					r="8"
					cx="9"
					cy="8.5"/>
			</svg>
		</div>
	</div>
</template>

<script>
export default {
	name: "Bullet",
	props:[
		"active",
		"tick"
	],
	methods:{
		setProgress: function(percent){
			var circle = document.getElementById('pr');
			//console.log(circle);
			var radius = circle.r.baseVal.value;
			var circumference = radius * 2 * Math.PI;

			circle.style.strokeDasharray = `${circumference} ${circumference}`;
			circle.style.strokeDashoffset = `${circumference}`;
			const offset = circumference - percent / 60 * circumference;
			circle.style.strokeDashoffset = offset;
		},
		getTick: function(){
			setInterval(()=> {
				this.setProgress(2 * this.tick)
				console.log(this.tick)
			}, 200)
		},
		
	},
	mounted(){
		if(this.active == true) this.setProgress(this.progress);
		this.getTick()
	},
	data: function () {
		return {
			progress: 0
		}
	}
}
</script>

<style scoped>
.bullet{
	width: 16px;
	height: 16px;
}
.point{
	background: #FFF8;
	border-radius: 50%;
	width: 8px;
	height: 8px;
}
.circle{
	position: relative;
	left: -5px;
	top: -14px;
}
.progress-ring__circle {
	transition: 0.35s stroke-dashoffset;
	transform: rotate(-90deg);
	transform-origin: 50% 50%;
}
</style>